import React from "react";
import PropTypes from "prop-types";
import Link from "gatsby-link";
import Helmet from "react-helmet";

import "../scss/styles.scss";
import Socials from "./socials";
import "../scss/prism-styles";

const ListLink = props => (
  <li>
    <Link activeClassName="active" to={props.to}>
      {props.children}
    </Link>
  </li>
);

const Navigation = () => (
  <nav className="Navigation">
    <ul className="nav-list">
      <ListLink to="/">Home</ListLink>
      <ListLink to="/projects/">Projects</ListLink>
      <ListLink to="/blog/">Blog</ListLink>
    </ul>
  </nav>
);

const Header = () => (
  <div className="Header">
    <Link className="logo-link" to="/">
      <div className="diamond" />
      <div className="name">Aaron Ong</div>
    </Link>
    <Navigation />
  </div>
);

const TemplateWrapper = ({ children }) => {
  return (
    <div className="">
      <Helmet
        title="Aaron Ong - Full Stack Developer"
        meta={[
          {
            name: "description",
            content: "Aaron Ong - Front End Web Developer - Singapore"
          },
          {
            name: "keywords",
            content:
              "Aaron Ong, Singapore, Full Stack Developer, gatsbyjs, gatsby, blog, frontend, reactjs, react, css, scss, sass"
          }
        ]}
      />
      <Header />
      <div className="content-container">{children()}</div>
    </div>
  );
};

TemplateWrapper.propTypes = {
  children: PropTypes.func
};

export default TemplateWrapper;



